import {
  LOAD_PIZZAS,
  SET_CURRENT_FILTER,
  SET_CURRENT_SORTING_OPTION,
} from "../actions/app";
import { PizzaProps } from "../../types";

interface IActionTypes {
  type: string;
  payload: PizzaProps[] | string;
}

interface IInitialStateTypes {
  pizzas: PizzaProps[];
  filters: {
    [key: string]: string;
  };
  currentFilter: string;
  sortingOptions: {
    [key: string]: string;
  };
  currentSortingOption: string;
}

const initialState: IInitialStateTypes = {
  pizzas: [],
  filters: {
    all: "–í—Å–µ",
    meat: "–ú—è—Å–Ω—ã–µ",
    vegi: "–í–µ–≥–∏—Ç–∞—Ä–∏–∞–Ω—Å–∫–∏–µ",
    chicken: "–° –∫—É—Ä–∏—Ü–µ–π",
    fish: "–° —Ä—ã–±–æ–π",
    spicy: "–û—Å—Ç—Ä—ã–µ",
  },
  currentFilter: "all",
  sortingOptions: {
    default: "–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏",
    asc: "—Å–Ω–∞—á–∞–ª–∞ –¥–µ—à–µ–≤—ã–µ",
    desc: "—Å–Ω–∞—á–∞–ª–∞ –¥–æ—Ä–æ–≥–∏–µ",
    alph: "–∞–ª—Ñ–∞–≤–∏—Ç—É",
  },
  currentSortingOption: "default",
};

const appReducer = (state = initialState, { type, payload }: IActionTypes) => {
  switch (type) {
    case LOAD_PIZZAS:
      return {
        ...state,
        pizzas: payload,
      };
    case SET_CURRENT_FILTER:
      console.log("filter from set", payload);
      return {
        ...state,
        currentFilter: payload,
      };
    case SET_CURRENT_SORTING_OPTION:
      console.log("sorting option ", payload);
      return {
        ...state,
        currentSortingOption: payload,
      };
    default: {
      return {
        ...state,
      };
    }
  }
};

export { appReducer };
